.gift-container {
  height: 100%;
  overflow: hidden;

  .merrywrap {
    width: 100vw;
    height: 100vh;
    right: 40px;
    left: 40px;
    bottom: 40px;
    top: 40px;
    background-color: #e74c3c;
  }
  .giftbox {
    position: absolute;
    width: 300px;
    height: 200px;
    left: 50%;
    margin-left: -150px;
    margin-bottom: 10%;
    bottom: 0;
    z-index: 10;
    & > div {
      background: #34495e;
      position: absolute;
      &:after,
      &:before {
        position: absolute;
        content: "";
        top: 0;
      }
    }
    &:after {
      position: absolute;
      color: #fff;
      width: 100%;
      content: "Click Me!";
      left: 0;
      bottom: 0;
      font-size: 24px;
      text-align: center;
      transform: rotate(-20deg);
      transform-origin: 0 0;
    }
    .cover {
      width: 100%;
      top: 0;
      left: 0;
      height: 25%;
      z-index: 2;
      &:before {
        position: absolute;
        height: 100%;
        left: 50%;
        width: 50px;
        transform: translateX(-50%);
        background: #fdc56d;
      }
      & > div {
        position: absolute;
        width: 50px;
        height: 50px;
        left: 50%;
        top: -50px;
        transform: translateX(-50%);
        &:before,
        &:after {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          content: "";
          box-shadow: inset 0 0 0 15px #fdc56d;
          border-radius: 30px;
          transform-origin: 50% 100%;
        }
        &:before {
          transform: translateX(-45%) skewY(40deg);
        }
        &:after {
          transform: translateX(45%) skewY(-40deg);
        }
      }
    }
    .box {
      right: 5%;
      left: 5%;
      height: 80%;
      bottom: 0;
      &:before {
        width: 50px;
        height: 100%;
        left: 50%;
        transform: translateX(-50%);
        background: #fdc56d;
      }
      &:after {
        width: 100%;
        height: 30px;
        background: rgba(0, 0, 0, 0.2);
      }
    }
  }
  .icons {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: auto;
    transform: translateY(-50%);
    .row {
      width: 100%;
      text-align: center;
      span {
        color: #fff;
        text-shadow: 5px 5px 0 #ca3121;
        font-size: 84px;
        display: inline-block;
        opacity: 0;
        transition: transform 0.5s ease-in, opacity 0.7s;
      }
    }
  }
  .step-1 {
    .giftbox {
      animation: wobble 0.5s linear infinite forwards;
    }
    .cover {
      animation: wobble 0.5s linear infinite 0.1s forwards;
    }
    .icons .row span {
      opacity: 1;
    }
  }
  .step-2 .giftbox:after {
    opacity: 0;
  }
  .step-3 .giftbox,
  .step-4 .giftbox {
    opacity: 0;
    z-index: 1;
    &:after {
      opacity: 0;
    }
  }
  .step-2 {
    .giftbox {
      .cover {
        animation: flyUp 0.4s ease-in forwards;
      }
      .box {
        animation: flyDown 0.2s ease-in 0.05s forwards;
      }
    }
  }
  @keyframes wobble {
    25% {
      transform: rotate(4deg);
    }
    75% {
      transform: rotate(-2deg);
    }
  }
  @keyframes flyUp {
    75% {
      opacity: 1;
    }
    100% {
      transform: translateY(-1000px) rotate(20deg);
      opacity: 0;
    }
  }
  @keyframes flyDown {
    75% {
      opacity: 1;
    }
    100% {
      transform: translateY(100%);
      opacity: 0;
    }
  }
  .step-1 .icons .row span {
    opacity: 0;
  }
  .step-1 .icons .row span:first-child {
    transform: translateY(700%) translateX(600%);
  }
  .step-1 .icons .row span:nth-child(2) {
    transform: translateY(700%) translateX(500%);
  }
  .step-1 .icons .row span:nth-child(3) {
    transform: translateY(700%) translateX(400%);
  }
  .step-1 .icons .row span:nth-child(4) {
    transform: translateY(700%) translateX(300%);
  }
  .step-1 .icons .row span:nth-child(5) {
    transform: translateY(700%) translateX(200%);
  }
  .step-1 .icons .row span:nth-child(6) {
    transform: translateY(700%) translateX(100%);
  }
  .step-1 .icons .row span:nth-child(7) {
    transform: translateY(700%) translateX(0);
  }
  .step-1 .icons .row span:nth-child(8) {
    transform: translateY(700%) translateX(-100%);
  }
  .step-1 .icons .row span:nth-child(9) {
    transform: translateY(700%) translateX(-200%);
  }
  .step-1 .icons .row span:nth-child(10) {
    transform: translateY(700%) translateX(-300%);
  }
  .step-1 .icons .row span:nth-child(11) {
    transform: translateY(700%) translateX(-400%);
  }
  .step-1 .icons .row span:nth-child(12) {
    transform: translateY(700%) translateX(-500%);
  }
  .step-1 .icons .row span:nth-child(13) {
    transform: translateY(700%) translateX(-600%);
  }
  .step-1 .icons .row span:nth-child(14) {
    transform: translateY(700%) translateX(-700%);
  }
  .step-2 .icons .row span:nth-child(2) {
    -webkit-transition-delay: 0.1s;
    transition-delay: 0.1s;
  }

  .step-2 .icons .row span:nth-child(3) {
    -webkit-transition-delay: 0.15s;
    transition-delay: 0.15s;
  }

  .step-2 .icons .row span:nth-child(4) {
    -webkit-transition-delay: 0.2s;
    transition-delay: 0.2s;
  }

  .step-2 .icons .row span:nth-child(5) {
    -webkit-transition-delay: 0.25s;
    transition-delay: 0.25s;
  }

  .step-2 .icons .row span:nth-child(6) {
    -webkit-transition-delay: 0.3s;
    transition-delay: 0.3s;
  }

  .step-2 .icons .row span:nth-child(7) {
    -webkit-transition-delay: 0.35s;
    transition-delay: 0.35s;
  }
  .step-2 .icons .row span:nth-child(8) {
    -webkit-transition-delay: 0.4s;
    transition-delay: 0.4s;
  }
  .step-2 .icons .row span:nth-child(9) {
    -webkit-transition-delay: 0.45s;
    transition-delay: 0.45s;
  }
  .step-2 .icons .row span:nth-child(10) {
    -webkit-transition-delay: 0.5s;
    transition-delay: 0.5s;
  }
  .step-2 .icons .row span:nth-child(11) {
    -webkit-transition-delay: 0.55s;
    transition-delay: 0.55s;
  }
  .step-2 .icons .row span:nth-child(12) {
    -webkit-transition-delay: 0.6s;
    transition-delay: 0.6s;
  }
  .step-2 .icons .row span:nth-child(13) {
    -webkit-transition-delay: 0.65s;
    transition-delay: 0.65s;
  }
  .step-2 .icons .row span:nth-child(14) {
    -webkit-transition-delay: 0.65s;
    transition-delay: 0.65s;
  }
  .step-2 .icons span,
  .step-3 .icons span,
  .step-4 .icons span {
    opacity: 0.7;
    transition-timing-function: cubic-bezier(0, 0, 0.2, 0.91);
  }
  .step-4 .icons .row span,
  .step-3 .icons .row span {
    animation: wobble 0.6s linear infinite forwards;
  }

  .step-4 .icons .row span:nth-child(even),
  .step-3 .icons .row span:nth-child(even) {
    animation-duration: 0.7s;
  }

  a,
  a:hover {
    text-decoration: none;
  }

  .trendy_btn {
    display: block;
    margin: 5% auto;
    padding: 18px;
    text-align: center;
  }

  @keyframes fadein {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  span.txt {
    display: block;
    position: relative;
    z-index: 2;
  }

  /* code button TWO (like Lover) */

  a.button_two {
    position: relative;
    overflow: hidden;
    width: 260px;
    font-size: 1.4em;
    font-weight: 700;
    background-color: #b52122;
    color: #fff;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.7);
  }

  a.button_two:hover {
    background-color: #d33031;
  }

  a.button_two:active {
    top: 1px;
    -o-transition: none;
    -ms-transition: none;
    -moz-transition: none;
    -webkit-transition: none;
    transition: none;
  }

  a.button_two:before {
    display: inline-block;
    position: absolute;
    z-index: 1;
    width: 320px;
    top: -50%;
    left: -5%;
    font-size: 1.375em;
    font-family: "fontello";
    content: "\2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665  \2665";
    color: #8c1f20;
    text-shadow: none;
    -webkit-transform: rotate(-10deg);
    -moz-transform: rotate(-10deg);
    -o-transform: rotate(-10deg);
    -ms-transform: rotate(-10deg);
    transform: rotate(-10deg);
  }

  a.button_two span.txt:before {
    display: inline-block;
    margin: 0 10px 0 0;
    font-family: "fontello";
    content: "\2665";
    color: #fff;
  }
}
